const Tip = styled.div`
  width: 200px;
  padding: 15px 0px;
`;

const QuickInsertPanel: React.FC<PanelRenderProps> = props => {
  const { editor, locale = {}, close } = props;

  const handleInsert = React.useCallback(
    () => close(editor).run('onAction', selectFile()),
    [editor, close],
  );

  return (
    <CommonPanel placeholder={placeholder}>
      <Button onClick={handleInsert}>{locale.buttonTitle}</Button>
      <Tip>{locale.insertTip}</Tip>
    </CommonPanel>
  );
};

export default QuickInsertPanel;
